.richtext {
  position: relative;
  background-size: 100%;
}
  .richtext p {
    font-family: var(--font);
    font-weight: 400;
    line-height: 1.5;
    margin-bottom: 60px;
  }
  .richtext p:last-child {
      margin-bottom: 0;
    }
  .richtext:after {
    position: absolute;
    content: ' ';
    display: block;
    width: 150%;
    top: -25%;
    left: -25%;
    background-image: radial-gradient(white, rgba(255, 255, 255, 0));
    z-index: -1;
    height: 150%;
    max-height: 100vh;
    max-width: 100vw;
  }
  .richtext h1,
  .richtext h2,
  .richtext h3,
  .richtext h4,
  .richtext h5 {
    font-family: var(--font);
    line-height: 1.2;
    color: var(--navy);
  }
  .richtext h3 {
    text-shadow: initial;
    font-size: 28px;
    line-height: 32px;
    font-weight: 300;
  }
  .richtext h3:hover {
      text-shadow: initial;
    }
  .richtext a {
    color: inherit;
    overflow-wrap: break-word;
    word-wrap: break-word;

    -ms-word-break: break-all;
    /* This is the dangerous one in WebKit, as it breaks things wherever */
    word-break: break-all;
    /* Instead use this non-standard one: */
    word-break: break-word;

    /* Adds a hyphen where the word breaks, if supported (No Blink) */
    -ms-hyphens: auto;
    -webkit-hyphens: auto;
    hyphens: auto;

    font-weight: bold;
    color: var(--navy);
    border-bottom: 4px solid var(--teal);
    text-decoration: none;
  }
  .richtext ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }
  .richtext li {
    margin-bottom: 12px;
    padding-left: 24px;
    display: flex;
    align-items: flex-start;
    position: relative;
  }
  .richtext li:after {
      content: 'â€¢';
      font-size: 1em;
      height: 16px;
      line-height: 16px;
      color: var(--teal);
      position: absolute;
      display: block;
      top: 0.6ex;
      left: 0;
    }
