.team {
  margin-bottom: 80px;
}

.member {
  display: flex;
  padding: 28px 0;
  align-items: center;
}

@media (max-width: 520px) {
    .member {
      flex-direction: column;
      align-items: flex-start;
    }
  }

.role {
  font-weight: 500;
  font-size: 26px;
}

.memberName {
  font-size: 32px;
  font-weight: bold;
}

.memberImg {
  flex-shrink: 0;
  box-shadow: var(--schubrakeden-shadow--light);

  margin-right: 28px;
  height: 160px;
  border-radius: 160px;
  background-position: center center;
  background-size: cover;
  width: 160px;
}

@media (max-width: 520px) {
    .memberImg {
      margin-bottom: 28px;
    }
  }

.memberDescription {
  margin: 0;
  margin-top: 8px;
  padding-bottom: 8px;
}
