.button {
  background: var(--navy);
  border-radius: 4px;
  color: white;
  text-decoration: none;
  font-family: var(--font-headline);
  height: 56px;
  padding: 0 36px;
  display: inline-flex;
  font-size: inherit;
  align-items: center;
  font-weight: 600;

  justify-content: center;

  box-shadow: var(--schubrakeden-shadow);

  transition: box-shadow 120ms ease-in-out;
}

  @media (max-width: 520px) {.button {
    font-weight: 500;
    padding: 0 28px;
    height: 42px
}
  }

  .button:focus,
  .button:hover {
    box-shadow: 8px 8px 0 0 rgba(101, 133, 167, 0.3),
      4px 4px 0 0 rgba(134, 155, 177, 0.4);
  }

.secondary {
  color: var(--navy);
  background: var(--blueLight);

  box-shadow: var(--schubrakeden-shadow--light);
}

.secondary:focus,
  .secondary:hover {
    box-shadow: 8px 8px 0 0 rgba(131, 152, 153, 0.12),
      4px 4px 0 0 rgba(214, 225, 235, 0.56);
  }
