.header {
  text-align: center;

  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-bottom: 80px;
}
  @media (min-width: 520px) {
    .header {
      height: auto;
    }
  }

.headerText {
  margin-bottom: 36px;
}

.headerHeadline {
  font-weight: 600 !important;
}

.team {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 50px;
}

.subline {
  padding: 0 50px;
}

.mission {
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
  font-size: 32px;
}

@media (--small-screen) {
    .mission {
      font-size: 24px;
    }
  }
.teamPicture {
  width: 100%;
}
